@use 'base/variables' as v;

.meetings{
    list-style: none;
    padding: 0;
    li{
        padding: 1rem;
        border-top: 1px solid v.$blanco;
        p{
            font-weight: bold;
            color: v.$azul;
        }
        span,
        .service{
            font-weight: normal;
            color: v.$blanco;
        }
    }
}
.services{
    @extend  .meetings;
    .actions{
        margin: 0;
    }
    .button-delete,
    .button{
        margin: 0;
    }
}