@use 'base/mixins' as m;
@use 'base/variables' as v;

.list-services{
    display: grid;
    gap: 2rem;
    @include m.tablet{
        @include m.grid(2, 2rem);
    }
    .service{
        background-color: v.$blanco;
        padding: 3rem;
        border-radius: 1rem;
        transition-property: transform;
        transition-duration: 300ms;
        text-align: center;
        &:hover{
            transform: scale(1.1);
            cursor: pointer;
        }
        p{
            color: v.$negro;
            margin: 0 0 calc(v.$separacion / 4) 0;
        }
        .price-service{
            color: v.$azul;
            font-weight: bold;
            font-size: 3rem;
            margin: 0;
        }
        &.selected{
            background-color: v.$azul;
            p{
                color: v.$blanco;
            }
        }
    }
}