@use 'base/mixins' as m;
@use 'base/variables' as v;

.tabs{
    @include m.tablet{
        display: flex;
    }
    button{
        background-color: v.$azul;
        cursor: pointer;
        border: none;
        color: v.$blanco;
        padding: 1rem;
        display: block;
        width: 100%;
        margin-bottom: 1rem;
        text-transform: uppercase;
        font-weight: bold;
        transition: background-color 300ms ease;
        &:hover{
            background-color: darken(v.$azul, 10%);
        }
        &.current{
            background-color: v.$blanco;
            color: v.$azul;
        }
    }
}