@use 'base/variables' as v;
@use 'base/mixins' as m;

.container-app{
    @include m.tablet{
        @include m.grid(2, 1fr);
        min-height: 100vh;
    }
    .image{
        height: 20rem;
        background-image: url('../img/1.jpg');
        background-size: cover;
        background-position: center center;
        @include m.tablet{
            min-height: 100vh;
            height: 100%;
        }
    }
    .app{
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0;
        @include m.tablet{
            overflow-y: scroll;
        }
    }
}
.bar{
    margin: v.$separacion 0;
    @include m.tablet{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .button{
        @include m.boton(v.$negro, v.$blanco);
        margin: 0;
    }
}
.service-bar{
    margin-bottom: v.$separacion;
    @include m.tablet{
        display: flex;
        justify-content: space-between;
    }
    .button{
        margin: 0 0 1rem 0;
    }
}